import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { ProgramSetupComponent } from './program-setup.component';

import { ProgramSetupResolver } from './program-setup.resolver';

const routes: Routes = [
  { path: 'list', component:  ProgramSetupComponent,
  children: [
    {
      path: ":type/:id",
      loadChildren: () => import('src/app/modules/admin/program/program-setup/add-program-setup/add-program-setup.module').then(m => m.AddProgramSetupModule),
      outlet: "drawer",
      resolve: {
        ProgramSetupResolver
      }
    },
    {
      path: "view",
      loadChildren: () => import('src/app/modules/admin/program/program-setup/add-program-setup/add-program-setup.module').then(m => m.AddProgramSetupModule),
      outlet: "drawer",
      resolve: {
        ProgramSetupResolver
      }
    },
    { path: 'main', loadChildren: () => import('src/app/modules/admin/program/program-setup/main-program-setup/main-program-setup.module').then(m => m.MainProgramSetupModule)},
    { path: 'class', loadChildren: () => import('src/app/modules/admin/program/program-setup/class-level-program-setup/class-level-program-setup.module').then(m => m.ClassLevelProgramSetupModule)},
    { path: 'style', loadChildren: () => import('src/app/modules/admin/program/program-setup/style-program-setup/style-program-setup.module').then(m => m.StyleProgramSetupModule)},
    { path: 'batch', loadChildren: () => import('src/app/modules/admin/program/program-setup/batch-type-program-setup/batch-type-program-setup.module').then(m => m.BatchTypeProgramSetupModule)},
    { path: 'room', loadChildren: () => import('src/app/modules/admin/program/program-setup/room-program-setup/room-program-setup.module').then(m => m.RoomProgramSetupModule)},
    { path: 'mode', loadChildren: () => import('src/app/modules/admin/program/program-setup/mode-program-setup/mode-program-setup.module').then(m => m.ModeProgramSetupModule)},
    { path: '', redirectTo: 'main', pathMatch: 'full' }
  ]
  },
  { path: '', redirectTo: 'list', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProgramSetupRoutingModule { }
