  ngOnInit(): void {

    this.navArray = [
  		{link:'main', name: 'Main Program'},
  		{link:'class', name: 'Class Level'},
      {link:'style', name: 'Style'},
  		{link:'batch', name: 'Batch Type'},
  		{link:'room', name: 'Room'},
  		{link:'mode', name: 'Mode'}
  	]

    this.programSetupService.programstupmatdrawercast.subscribe((res:any) => {

      if(res != null) {
        if(res.id) {

          if(res.category == 'main'){
            if(!res.isedit){
              this.addMainProgram(res.id)
              if(res.data != null){
                this.viewMainProgram(res.id)
              }
            }
            else
              this.editMainProgram(res.id)
          }
          else if(res.category == 'class') {
            if(!res.isedit){
              this.addClassLevelProgram(res.id)
              if(res.data != null){
                this.viewClassLevelProgram(res.id)
              }
            }
            else
              this.editClassLevelProgram(res.id)
          }
          else if(res.category == 'style') {
            if(!res.isedit){
              this.addStyleProgram(res.id)
              if(res.data != null){
                this.viewStyleProgram(res.id)
              }
            }
            else
              this.editStyleProgram(res.id)
          }
          else if(res.category == 'batch') {
            if(!res.isedit){
              this.addBatchTypeProgram(res.id)
              if(res.data != null){
                this.viewBatchTypeProgram(res.id)
              }
            }
            else
              this.editBatchTypeProgram(res.id)
          }
          else if(res.category == 'room') {
            if(!res.isedit){
              this.addRoomProgram(res.id)
              if(res.data != null){
                this.viewRoomProgram(res.id)
              }
            }
            else
              this.editRoomProgram(res.id)
          }
          else if(res.category == 'mode') {
            if(!res.isedit){
              this.addModeProgram(res.id)
              if(res.data != null){
                this.viewModeProgram(res.id)
              }
            }
            else
              this.editModeProgram(res.id)
          }
        }


      }

    })

  }

